.cards {
  padding: 4rem;
  background: #fff;
}

h1 {
  text-align: center;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 70em;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 3.125em 0 2.8125em;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 0.625em;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 0.375em 1.25em rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 0.375em 1.25em rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 0.375em 1.25em rgba(56, 125, 255, 0.017));
  border-radius: 0.625em;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 0.625em;
  padding: 0.375em 0.5em;
  max-width: calc((100%) - 3.75em);
  font-size: 0.75rem;
  font-weight: 700;
  color: #fff;
  background-color: rgb(37, 87, 87);
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 1.25em 1.875em 1.875em;
}

.cards__item__text {
  color: #252e48;
  font-size: 1.125rem;
  line-height: 1.5rem;
}

@media only screen and (min-width: 75em) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 64em) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 64em) {
  .cards__items {
    margin-bottom: 2rem;
  }
}
